___CSS_LOADER_EXPORT___.push([module.id, `/* Reset */
:root {
  --primary: #3a86ff;
  --bg: #f0f4f8;
  --text: #222;
  --label: #555;
  --card-bg: #fff;
  --border-radius: 12px;
  --shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  html,
  body {
    height: 100%;
    margin: 0;
  }

  body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: system-ui, sans-serif;
    background-color: #f4f4f4;
    color: #333;
  }

  /* Header y Footer fijos arriba y abajo */
  header,
  footer {
    background-color: #333;
    color: white;
    padding: 1rem;
    text-align: center;
    flex-shrink: 0;
  }

  /* Main ocupa todo el espacio vertical restante */
  main {
    flex: 1;
    width: 100%;
    padding: 2rem 1rem;

    display: flex;
  align-items: center;
  justify-content: center;
  }

  #app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }

#weatherContainer {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  background-color: var(--card-bg);
  padding: 2rem;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  max-width: 600px;
  margin: auto;
}


#weatherContainer h2 {
  margin: 0 auto 1.5rem auto; /* ya incluye margin-bottom */
  color: var(--primary);
  font-size: 2rem;
  text-align: center;
  border-bottom: 2px solid var(--primary);
  padding-bottom: 0.5rem;
}

  .weather-grid {
    display: flex;
  gap: 2rem;
  justify-content: center;
  flex-wrap: wrap;
  }

  .label {
    font-weight: 600;
    color: var(--label);
    display: inline-block;
    min-width: 150px;
  }

  .day-block {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    padding: 1rem 1.5rem;
}

.day-block h3 {
    margin-top: 0;
    color: var(--primary);
}

  /* Responsive: centrado de contenido en pantallas grandes */
  @media (min-width: 768px) {
    main {
      max-width: 100%;
      margin: 0 auto;
    }
  }`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,UAAU;AACV;EACE,kBAAkB;EAClB,aAAa;EACb,YAAY;EACZ,aAAa;EACb,eAAe;EACf,qBAAqB;EACrB,uCAAuC;AACzC;;EAEE;IACE,sBAAsB;IACtB,SAAS;IACT,UAAU;EACZ;;EAEA;;IAEE,YAAY;IACZ,SAAS;EACX;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,kCAAkC;IAClC,yBAAyB;IACzB,WAAW;EACb;;EAEA,yCAAyC;EACzC;;IAEE,sBAAsB;IACtB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,cAAc;EAChB;;EAEA,iDAAiD;EACjD;IACE,OAAO;IACP,WAAW;IACX,kBAAkB;;IAElB,aAAa;EACf,mBAAmB;EACnB,uBAAuB;EACvB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,iBAAiB;EACnB;;AAEF;EACE,aAAa;EACb,sBAAsB;EACtB,qBAAqB;EACrB,uBAAuB;EACvB,uBAAuB;EACvB,eAAe;EACf,gCAAgC;EAChC,aAAa;EACb,mCAAmC;EACnC,yBAAyB;EACzB,gBAAgB;EAChB,YAAY;AACd;;;AAGA;EACE,0BAA0B,EAAE,6BAA6B;EACzD,qBAAqB;EACrB,eAAe;EACf,kBAAkB;EAClB,uCAAuC;EACvC,sBAAsB;AACxB;;EAEE;IACE,aAAa;EACf,SAAS;EACT,uBAAuB;EACvB,eAAe;EACf;;EAEA;IACE,gBAAgB;IAChB,mBAAmB;IACnB,qBAAqB;IACrB,gBAAgB;EAClB;;EAEA;IACE,mBAAmB;IACnB,mBAAmB;IACnB,qCAAqC;IACrC,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,qBAAqB;AACzB;;EAEE,2DAA2D;EAC3D;IACE;MACE,eAAe;MACf,cAAc;IAChB;EACF","sourcesContent":["/* Reset */\r\n:root {\r\n  --primary: #3a86ff;\r\n  --bg: #f0f4f8;\r\n  --text: #222;\r\n  --label: #555;\r\n  --card-bg: #fff;\r\n  --border-radius: 12px;\r\n  --shadow: 0 4px 20px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n  * {\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n    padding: 0;\r\n  }\r\n\r\n  html,\r\n  body {\r\n    height: 100%;\r\n    margin: 0;\r\n  }\r\n\r\n  body {\r\n    display: flex;\r\n    flex-direction: column;\r\n    min-height: 100vh;\r\n    font-family: system-ui, sans-serif;\r\n    background-color: #f4f4f4;\r\n    color: #333;\r\n  }\r\n\r\n  /* Header y Footer fijos arriba y abajo */\r\n  header,\r\n  footer {\r\n    background-color: #333;\r\n    color: white;\r\n    padding: 1rem;\r\n    text-align: center;\r\n    flex-shrink: 0;\r\n  }\r\n\r\n  /* Main ocupa todo el espacio vertical restante */\r\n  main {\r\n    flex: 1;\r\n    width: 100%;\r\n 